import React, { useEffect, useMemo } from 'react'
import { graphql, navigate } from 'gatsby'
import { useFormikContext } from 'formik';
import { TiArrowLeft } from "react-icons/ti";
import { IconButton } from '@chakra-ui/react'
import { set } from 'lodash';
import AniLink from "gatsby-plugin-transition-link/AniLink"

import Box from '../components/Box'
import Link from '../components/Link'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Container from '../components/Container'

import Layout from '../containers/Layout'
import score from '../json/score';

const Question = ({ data, pageContext: { id, isLast, length } }) => {
  const { values, submitForm } = useFormikContext()
  useEffect(() => {
    if(values.answers.length < +id) {
      navigate(`/q/${values.answers.length + 1}`)
    }
  }, [values.answers, id])
  const calculateScore = Object.values(score)
  const totolPoints = calculateScore.reduce((res, scores) => {
    const point = scores.reduce((total, num, i) => {
      total = total + (num[values.answers[i]] || +'0')
      return total
    }, 0)
    res.push(point)
    return res
  },[])
  console.log(values, calculateScore, totolPoints)
  return (
    <Layout title={`第${id}題`}>
      <Box bg="white" height="100%">
        <Container>
          <Flex alignItems="center">
            {id > 1 && <IconButton as={AniLink} swipe direction="right" to={`/q/${id - 1}`} mr="0.25em" minWidth="auto" w="1.5em" h="1.5em" isRound bg="black" color="white" icon={<TiArrowLeft size="1.5em" />} />}
            <Text fontWeight="bold"><Text.Inline fontSize="2em">{id}</Text.Inline>/ {length}</Text>
          </Flex>
          <Text>{data?.questionsJson.question}</Text>
          <Box>
            {data?.questionsJson.options.map((opt, i) => (
              <Box mt="1em" key={i}>
                <Button
                  as={!isLast && ((p) => <AniLink top="entry" {...p} />)}
                  onClick={() => {
                    set(values, ['answers', +id - 1], i)
                    if(isLast) {
                      submitForm()
                    }
                  }}
                  to={!isLast && `/q/${+id + 1}`}
                  swipe
                  direction="left"
                  width="100%"
                >
                  {opt}
                </Button>
              </Box>
            ))}
          </Box>
        </Container>
      </Box>
    </Layout>
  )
}

export default Question

export const query = graphql`
  query QuestionPageQuery($id: String) {
    questionsJson(id: {eq: $id}) {
      question
      options
    }
  }
`


